  <div id="Room" class="container">
    <div class="row banner">
      <img src="" />
    </div>
    <div class="row roomInfo">
      <div class="col-8 left">
        <div class="roomIntroduction">
          <h1>{{ this.room.name }}</h1>
          <p>{{ this.room.description }}</p>
          <div>
             ${{ this.room.normalDayPrice }}
            <!-- spanmargin -->
            <span>  ${{ this.room.holidayPrice }} </span>
          </div>
        </div>

        <div class="row rooomSpecification">
          <div class="col-6">
            <ul>
              <li>{{ getGuestLimit() }}</li>
              <li>{{ getBedType() }}</li>
              <li>
                {{ this.room.descriptionShort["Private-Bath"] }}
              </li>
            </ul>
          </div>
          <div class="col-6">
            <ul>
              <li>
                {{ this.room.descriptionShort.Footage }} 
              </li>
              <li>
                Check In {{ this.room.checkInAndOut.checkInEarly }} -
                {{ this.room.checkInAndOut.checkInLate }}
              </li>
              <li>Check Out {{ this.room.checkInAndOut.checkOut }}</li>
            </ul>
          </div>
        </div>

        <div class="row roomService">
          <div class="col-3">
            <ul>
              <li>Wifi</li>
              <li></li>
              <li>Mini Bar</li>
            </ul>
